<template>
<div id="page-top">
  <!-- Page Wrapper -->
  <div id="wrapper">

    <!-- Content Wrapper -->
    <div id="content-wrapper" class="d-flex flex-column">

    <div>
      <div class="text-center">
        <a class="small"><router-link :to="{path:'/usermanage'}">User</router-link></a>
      </div>
      <div class="text-center">
        <a class="small"><router-link :to="{path:'/bookmanage'}">Book</router-link></a>
      </div>
    </div>

      <!-- Main Content -->
      <div id="content">

        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

          <ul class="navbar-nav ml-auto">
            <li class="nav-item dropdown no-arrow d-sm-none">
              <a class="nav-link dropdown-toggle" href="#" id="searchDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <i class="fas fa-search fa-fw"></i>
              </a>
              <!-- Dropdown - Messages -->
              <div class="dropdown-menu dropdown-menu-right p-3 shadow animated--grow-in" aria-labelledby="searchDropdown">
                <form class="form-inline mr-auto w-100 navbar-search">
                  <div class="input-group">
                    <input type="text" class="form-control bg-light border-0 small" placeholder="Search for..." aria-label="Search" aria-describedby="basic-addon2">
                    <div class="input-group-append">
                      <button class="btn btn-primary" type="button">
                        <i class="fas fa-search fa-sm"></i>
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </li>
          </ul>
        </nav>

        <!-- Begin Page Content -->
        <div class="container-fluid">

          <!-- Page Heading -->
          <h1 class="h3 mb-2 text-gray-800">Users</h1>
          <button class="btn btn-primary btn-user" v-on:click="handleUserClick()">用户浏览</button>
          <!-- <p class="mb-4">DataTables is a third party plugin that is used to generate the demo table below. For more information about DataTables, please visit the <a target="_blank" href="https://datatables.net">official DataTables documentation</a>.</p> -->

          <!-- DataTales Example -->
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">Book Store</h6>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <div>Filter by Name:</div>
                <input class="form-control bg-light border-0 small" v-model="filters.name.value"/>

                <div>
                  <div>
                    <v-table
                        :data="tableData"
                        :filters="filters"
                        selectionMode="multiple"
                        selectedClass="table-info"
                        @selectionChanged="selectedRows = $event"
                        class="table table-bordered"
                        id="dataTable"
                        width="100%"
                        cellspacing="0"
                    >
                      <thead slot="head">
                        <th>Name</th>
                        <th>Writer</th>
                        <th>Price</th>
                        <th>ISBN</th>
                        <th>Inventory</th>
                        <th>Action</th>
                      </thead>
                      <tbody slot="body" slot-scope="{displayData}">
                        <v-tr
                            v-for="row in displayData"
                            :key="row.id"
                            :row="row"
                        >
                          <td>{{ row.name }}</td>
                          <td>{{ row.writer }}</td>
                          <td>{{ row.price }}</td>
                          <td>{{ row.isbn }}</td>
                          <td>{{ row.inventory }}</td>
                          <td>
                            <router-link :to="{path: '/about'}">show details</router-link>
                          </td>
                        </v-tr>
                      </tbody>
                    </v-table>
                    <strong>Selected:</strong>
                    <div v-if="selectedRows.length === 0" class="text-muted">No Rows Selected</div>
                    <ul>
                      <li v-for="selected in selectedRows" :key="selected.id">
                        {{ selected.name }}
                      </li>
                    </ul>
                    <smart-pagination :currentPage.sync="currentPage" :totalPages="totalPages"/>
                    <div>
                      <div>
                        <button class="btn btn-primary btn-user" v-on:click="handleOpenClick()">启用</button>
                      </div>
                      <br/>
                      <div>
                        <button class="btn btn-primary btn-user" v-on:click="handleCloseClick()">禁用</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- End of Main Content -->
      <!-- Footer -->
      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; EBOOK Store 2019</span>
          </div>
        </div>
      </footer>
      <!-- End of Footer -->
    </div>
    <!-- End of Content Wrapper -->
  </div>
  <!-- End of Page Wrapper -->
  <!-- Scroll to Top Button-->
  <a class="scroll-to-top rounded" href="#page-top">
    <i class="fas fa-angle-up"></i>
  </a>
  <!-- Logout Modal-->
  <div class="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Ready to Leave?</h5>
          <button class="close" type="button" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">×</span>
          </button>
        </div>
        <div class="modal-body">Select "Logout" below if you are ready to end your current session.</div>
        <div class="modal-footer">
          <button class="btn btn-secondary" type="button" data-dismiss="modal">Cancel</button>
          <a class="btn btn-primary" href="login.html">Logout</a>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'

export default {
  name: 'table',
  data () {
    return {
      showDialog: true,
      loadingBook: true,
      loadingUser: true,
      booksTableData: [],
      usersTableData: [],
      currentPage: 1,
      totalPage: 0,
      selectedRows: [],
      filters: {
        name: { value: '', keys: ['name'] }
      }
    }
  },
  methods: {
    handleUserClick () {
      http
        .get('/user')
        .then(response => {
          this.usersTableData = response.data
        }).finally(this.loadingUser = false)
        .catch(e => {
          console.log(e)
        })
    },
    handleOpenClick () {
      http
        .post('/user/active', this.selectedRows)
        .then(response => {
            handleUserClick()
        }).finally()
        .catch(e => {
          console.log(e)
        })
    },
    handleCloseClick () {
      http
        .post('/user/disactive', this.selectedRows)
        .then(response => {
            handleUserClick()
        })
        .catch(e => {
          console.log(e)
        })
    }
  },
  mounted() {
    http
      .get('/user')
      .then(response => {
        this.usersTableData = response.data
      }).finally(this.loadingUser = false)
      .catch(e => {
        console.log(e)
      })
  },
}
</script>

<style>

</style>



